import { useRef, useState } from 'react';
import Chess from 'chess.js';
import { Chessboard } from 'react-chessboard';
import evaluate from './Evaluate';

export default function App({ boardWidth }) {
  const chessboardRef = useRef();
  const [game, setGame] = useState(new Chess());
  const [boardOrientation, setBoardOrientation] = useState('white');
  const [currentTimeout, setCurrentTimeout] = useState(undefined);

  function safeGameMutate(modify) {
    setGame((g) => {
      const update = { ...g };
      modify(update);
      return update;
    });
  }

  function minimax(depth, game, a, b, maxmizing) {
    if (depth === 0) {
      return evaluate(game.board(), maxmizing);
    }

    var score = maxmizing ? -20000 : 20000;
    for (const move of game.moves()) {
      game.move(move);
      if (maxmizing) {
        score = Math.max(score, minimax(depth-1, game, a, b, !maxmizing));
      } else {
        score = Math.min(score, minimax(depth-1, game, a, b, !maxmizing));
      }
      game.undo();

      if (maxmizing) {
        if (score >= b) break;
        a = Math.max(a, score);
      }
      else if (!maxmizing) {
        if (score <= a) break;
        b = Math.max(b, score);
      }
    }
    return score;
  }

  function getBestMove(game, turn, depth) {
    var bestScore = turn ? -20000 : 20000;
    var c = -20000;
    var d = 20000;
    var bestMove = null;
    for (var move of game.moves()) {
      game.move(move);
      var score = minimax(depth-1, game, c, d, !turn);
      game.undo();
      console.log(move, score);
      if (turn && score > bestScore) {
        bestScore = score;
        bestMove = move;
      } else if (!turn && score < bestScore) {
        bestScore = score;
        bestMove = move;
      }

      if (turn) {
        c = Math.max(c, score);
      } else {
        d = Math.min(d, score);
      }
    }

    return bestMove;
  }

  function makeMove() {
    const move = getBestMove(game, game.turn() === 'w', 4);

    // exit if the game is over
    if (game.game_over() || game.in_draw() || move === null) return;

    safeGameMutate((game) => {
      game.move(move);
    });
  }

  function onDrop(sourceSquare, targetSquare) {
    const gameCopy = { ...game };
    const move = gameCopy.move({
      from: sourceSquare,
      to: targetSquare,
      promotion: 'q' // always promote to a queen for example simplicity
    });
    setGame(gameCopy);

    // illegal move
    if (move === null) return false;

    // store timeout so it can be cleared on undo/reset so computer doesn't execute move
    const newTimeout = setTimeout(makeMove, 200);
    setCurrentTimeout(newTimeout);
    return true;
  }

  return (
    <div>
      <Chessboard
        id="PlayVsRandom"
        animationDuration={200}
        boardOrientation={boardOrientation}
        boardWidth={boardWidth}
        position={game.fen()}
        onPieceDrop={onDrop}
        customBoardStyle={{
          borderRadius: '4px',
          boxShadow: '0 5px 15px rgba(0, 0, 0, 0.5)'
        }}
        ref={chessboardRef}
      />
      <button
        className="rc-button"
        onClick={() => {
          safeGameMutate((game) => {
            game.reset();
          });
          // stop any current timeouts
          clearTimeout(currentTimeout);
        }}
      >
        reset
      </button>
      <button
        className="rc-button"
        onClick={() => {
          setBoardOrientation((currentOrientation) => (currentOrientation === 'white' ? 'black' : 'white'));
        }}
      >
        flip board
      </button>
      <button
        className="rc-button"
        onClick={() => {
          safeGameMutate((game) => {
            game.undo();
          });
          // stop any current timeouts
          clearTimeout(currentTimeout);
        }}
      >
        undo
      </button>
    </div>
  );
}